import React, { Component } from 'react';

class Services extends Component {
    render(){
        return (
            <div className={"services"} >
                <div className={"wrapper"}>
                    <h1>Our Services</h1>
                    <div className={"content"}>
                        <div className={"left"}>
                            <div>
                                <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="506" height="524" viewBox="0 0 506 524">
                                    <image id="GEAR_WHEELS" dataName="GEAR WHEELS" width="506" height="524" xlinkHref="data:img/png;base64,"/>
                                </svg>
                            </div>
                        </div>
                        <div className={"right"}>
                            all services go here.
                        </div>
                    </div>


                </div>
            </div>
        );
    }
}

export default Services;